

/**********/

/* Colors */

/**********/

@white: #ffffff;
@black: #000000;
@ugly-blue: #2b8194;
@tealish: #27aca1;
@dusty-orange: #f26d36;
@black-two: #000000;
@tomato: #f05223;


/*********/

/* Fonts */

/*********/

@font-face {
    font-family: robotoRegular;
    src: url(../fonts/Roboto/Roboto-Regular.ttf);
}

@font-face {
    font-family: robotoBold;
    src: url(../fonts/Roboto/Roboto-Bold.ttf);
}


/******************/

/* General Styles */

/******************/

body {
	margin: 0;
	background-color: @dusty-orange;
	font-family: robotoRegular;

	#wrapper {

		border: 80px solid rgba(0, 0, 0, 0.3);

		.border-elements {
			.portinos-logo,
			.menu,
			.languages,
			.social,
			.contact {
				position: absolute;
			}
			.portinos-logo,
			.menu,
			.languages {
				top: 10px;
			} 
			.portinos-logo {
				left: calc(50% - 26px);
			}
			.menu {
				left: 10px;
				width: 43px;
				height: 43px;
				overflow: hidden;
			}
			.languages {
				right: 10px;
				color: @white;
			}
			.contact,
			.social {
				top: 50%;
			}
			.social {
				left: 15px;
			}
			.contact {
				right: 15px;
			}
		}
		.takeOverFirstScreen {
			position: relative;
		    bottom: 25px;
		    left: calc(50% - 70px);
		    background-color: #fff;
		    padding: 12px;
		    display: inline-block;
		}
	}
}




